      // 控制有消息页面和无消息页面之间的切换
      isBox: false
    };
  },
  methods: {
    // 点击 tab 栏数据
    tabsChange(index) {
      this.current = index;
    },
    // 去绿色健康订单的后续其(退款、取消、已完成、待收货、待发货、)他页面
    goGreenOrderDetails() {
      uni.navigateTo({
        url: "/pages/greenOrderDetails/greenOrderDetails"
      });
    },
    goGreenStayPayment() {
      uni.navigateTo({
        url: "/pages/greenStayPayment/greenStayPayment"
      });
    },
    scrollChange() {
      console.log(22)
    },
